import React, { Component } from 'react';
import List from '../components/List';
import Input from '../components/Input';

class TodoList extends Component{
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[
            ]
        }
    }

    handleChange=(e)=>{
        this.setState({inpValue:e.target.value})  //将输入的值赋值给inpValue
    }

    componentDidMount(){  //在第一次渲染后调用，只在客户端
        let todos = localStorage.getItem('todos'); //获得本地存储中的数据
        if(todos){
            this.setState({
                todos:JSON.parse(todos) //将todos字符串转换成对象，然后赋给state中的todos
            })
        }
    }

    componentDidUpdate(){  //在组件完成更新后立即调用，在初始化时不会被调用
        localStorage.setItem(  //数据更新之后调用，把更新过后的数据保存到本地存储中
            'todos',  //key值为todos
            JSON.stringify(this.state.todos)  //value值为转换成字符串的更改过后的state.todos
        )
    }


    addTodo = () =>{   //添加todo
        if(this.state.inpValue==''){
            return;
        }
        this.setState({  //向其中添加内容
            todos:[     //添加todos对象,title为输入框输入内容，done默认为false(未完成工作)
                {
                    title:this.state.inpValue,
                    done:false
                },
                ...this.state.todos //将数组列出
            ]
        })
        this.setState({inpValue:''});
    }

    delTodo = (idx)=>{   //删除todo内容
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!==idx)  //利用filter过滤
            //filter()方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素
            //删除内容为idx，index为数组的下标，当数组下标不等于要删除的idx时，留下此数据。相当于删除idx
        })
    }

    toggle=(index)=>{   //打勾或取消打勾
        let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos:newTodos //改变todos的done
        })
    }


    render(){
        const{todos,inpValue}=this.state;
        let arr1 = todos.filter(item=>!item.done)
        let arr2 = todos.filter(item=>item.done)
        return(
            <div>
                <h2>TodoList</h2>
                <div>
                    <Input data={inpValue} id='inp' handleChange={this.handleChange} addTodo={this.addTodo}/>
                    <h2>未完成{arr1.length}</h2>
                    <List data={todos} done={false} toggle={this.toggle} delTodo={this.delTodo}/>   {/* 把state里面的todos传递给子子件List */}
                    <h2>已完成{arr2.length}</h2>
                    <List data={todos} done={true} toggle={this.toggle} delTodo={this.delTodo}/>   {/* 把state里面的todos传递给子子件List */}
                </div>
            </div>
        )
    }
}

export default TodoList