import React, { Component } from 'react';



const List=(props)=>{
  return props.data.map((todo,idx)=>{
    if(props.done===todo.done){
        return <li  key={idx}>
            <input
             onChange={()=>props.toggle(idx)}
             checked={props.done}
             type="checkbox" id="inp"
            />
            <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
            <button onClick={()=>props.delTodo(idx)}>删除</button>
        </li>
    }
  })
}

export default List;



























